<template>
  <div class="container">
    <Meter />

    <!-- 圖片輪播 -->
    <div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">
      <ol class="carousel-indicators">
        <li data-target="#carouselExampleIndicators" data-slide-to="0" class="active"></li>
        <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
        <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
      </ol>
      <div class="carousel-inner">
        <div class="carousel-item active">
          <img src="@/assets/images/a.jpg" class="d-block w-100" height="400" alt="..." />
        </div>
        <div class="carousel-item">
          <img src="@/assets/images/b.jpg" class="d-block w-100" height="400" alt="..." />
        </div>
        <div class="carousel-item">
          <img src="@/assets/images/c.jpg" class="d-block w-100" height="400" alt="..." />
        </div>
      </div>
      <a
        class="carousel-control-prev"
        href="#carouselExampleIndicators"
        role="button"
        data-slide="prev"
      >
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Previous</span>
      </a>
      <a
        class="carousel-control-next"
        href="#carouselExampleIndicators"
        role="button"
        data-slide="next"
      >
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Next</span>
      </a>
    </div>

    <marquee class="mt-3 danger" style="color:#4caf50" direction="left">
      <h5>慶祝開台~~輸入優惠碼666,全館八折</h5>
    </marquee>

    <div class="row mt-2">
      <div class="col-md-3">
        <!-- 左側選單 (List group) -->
        <div class="list-group sticky-top" style="border-radius:15px">
          <a
            href="#"
            class="list-group-item list-group-item-action"
            @click.prevent="searchText = ''"
            :class="{ 'convert': searchText === ''}"
          >
            <i class="fas fa-pepper-hot"></i> 全部顯示
          </a>

          <a
            class="list-group-item list-group-item-action"
            href="#"
            @click.prevent="searchText = item"
            :class="{ 'convert': item === searchText}"
            v-for="item in categories"
            :key="item"
          >
            <i class="fas fa-pepper-hot"></i>
            {{ item }}
          </a>
        </div>
      </div>

      <div class="col-md-9">
        <div class="row">
          <div class="col-md-4 mb-4" v-for="(item) in filterData" :key="item.id">
            <div class="card border-0 shadow-sm">
              <div
                style="height: 150px; background-size: cover; background-position: center"
                :style="{backgroundImage:`url(${item.imageUrl}`}"
              ></div>
              <div class="card-body">
                <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
                <h5 class="card-title">
                  <a href="#" class="text-dark">{{ item.title }}</a>
                </h5>
                <p class="card-text">{{ item.content | subContent}}</p>
                <div class="d-flex justify-content-between align-items-baseline">
                  <div class="h5" v-if="!item.price">原價 {{ item.origin_price }} 元</div>
                  <del class="h6" v-if="item.price">原價 {{ item.origin_price }} 元</del>
                  <div class="h5 text-danger" v-if="item.price">特價 {{ item.price }} 元</div>
                </div>
              </div>
              <div class="card-footer d-flex">
                <button
                  type="button"
                  class="btn btn-outline-secondary btn-sm"
                  @click="getProduct(item.id)"
                >
                  <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                  查看更多
                </button>
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm ml-auto"
                  @click="addtoCart(item.id)"
                >
                  <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                  加到購物車
                </button>
              </div>
            </div>
          </div>
        </div>

        <!-- <Pagination :pages="pagination" @getProducts="getProducts" class="pagination justify-content-center" /> -->
      </div>
    </div>

    <!-- 購物Modal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt />
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
              <del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
              <div class="h4" v-if="product.price">特價 {{ product.price }} 元</div>
            </div>
            <select name class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">選購 {{num}} {{product.unit}}</option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計
              <strong>{{ product.num * product.price }}</strong> 元
            </div>
            <button
              type="button"
              class="btn btn-primary"
              @click="addtoCart(product.id, product.num)"
            >
              <i class="fas fa-spinner fa-spin" v-if="product.id === status.loadingItem"></i>加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <Footer />
  </div>
</template>
<script>
import $ from "jquery";
import Meter from "@/components/Meter";
import Footer from "@/components/Footer";
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
    Meter,
    Footer
    // Pagination
  },
  data() {
    return {
      product: {},
      status: {
        loadingItem: ""
      },

      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        }
      },
      searchText: "",

      coupon_code: []
    };
  },

  methods: {
    ...mapActions("productsModules", ["getProducts"]),
    ...mapActions("cartModules", ["getCart", "removeCart"]),

    getProduct(id) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      vm.status.loadingItem = id;
      this.$http.get(url).then(response => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        console.log(response);
        vm.status.loadingItem = "";
      });
    },

    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty
      };
      this.$http.post(url, { data: cart }).then(response => {
        console.log("加入購物車", response);
        vm.status.loadingItem = "";
        vm.getCart();
        $("#productModal").modal("hide");
      });
    }
  },

  computed: {
    filterData() {
      const vm = this;
      if (vm.searchText) {
        return vm.products.filter(item => {
          const data = item.category
            .toLowerCase()
            .includes(vm.searchText.toLowerCase());
          return data;
        });
      }
      return this.products;
    },

    ...mapGetters("productsModules", ["categories", "products"]),
    ...mapGetters("cartModules", ["cart"])
  },

  created() {
    this.getProducts();
    this.getCart();
  }
};
</script>
<style>
.convert {
  background-color: rgb(77, 202, 108) !important;
